import React, {Component} from 'react';
import PureRenderMixin from 'react-addons-pure-render-mixin'

import {Anchor,Row,Col,Breadcrumb,Button, Input} from 'antd';
const Search = Input.Search;
import {HashRouter as Router,Route,Link,Redirect,Switch} from 'react-router-dom'
let BASE_URL = process.env.NODE_ENV == "production" ? "https://loulan.lianlianchains.com/" : ""


import './style.scss';


import Zhineng from 'images/zhineng.png';
import Tongzhi from 'images/tongzhi.png';
import Qrcode from 'images/qrcode.png'



class Content extends Component {
    constructor(props) {
        super(props);
        this.shouldComponentUpdate = PureRenderMixin.shouldComponentUpdate.bind(this)
        this.state = {
            showAll: "block",
            headerNav: [{
                name: ""
            }, {
                name: ""
            }, {
                name: ""
            }],

        }
    }

    toggleLogin = () => {
        this.props.logFn();
    }

    getChainsList = () => {
        const data = this.props.chainsList;

        if(data && data.length) {
            return data
        }

    }

    tableView(data, id){
        console.log(id)
        this.props.showChainsTable(data, id);
    }

    goZhixng() {
        location.replace("/" + location.hash + "/perform")
    }

    hideShowButton = (index, types) => {
        this.props.showAndHide(index, types);
    }

    getContractShopList = () => {
        let data = this.props.contractShopList;

        return data;
    }

    render() {
        return (
            <Row type="flex" justify="center" className="develop-content">
                <Col className="top" span={16}>
                    <Breadcrumb separator=">">
                        <Breadcrumb.Item className="BreadcrumbItem" href="/#/"></Breadcrumb.Item>
                        <Breadcrumb.Item className="BreadcrumbItem" href="/#/platform"></Breadcrumb.Item>
                    </Breadcrumb>

                    <h2 className="title"></h2>

                    <Row type="flex" justify="center" className="chains-intent">
                        <Col span={18} className="content">
                            <div className="top">
                                <div><text className="num"></text></div>
                                <div><text className="num"></text></div>
                                <div><text className="num"></text>URT</div>
                                <div><text className="num"></text>GOLANG</div>
                            </div>


                            <div className="bottom">
                                <div><span className="button"><a href={`https://www.lianlianchains.com/smartcontract/lltemplet.go`}></a></span></div>
                                <div><span className="button"><a href={`https://www.lianlianchains.com/smartcontract/lltemplet.docx`}></a></span></div>
                            </div>
                        </Col>
                    </Row>

                    <div className="publish"><Link to="/platform/contract/upload"></Link></div>

                </Col>
            </Row>


        )
    }
}

export default Content;


